import itemList from "component/item-list.vue"
export default {
	components: {
		itemList
	},
	data() {
		return {
			/* 轮播图 */
			swiperList: [{}, {}],
			/* 商家列表 */
			itemList: [{}, {}, {}, {}, {}, {}]
		}
	},
	onReachBottom() {
		uni.showLoading({
			title: '加载中',
			mask: true
		})
		setTimeout(function() {
			uni.hideLoading()
		}, 1000);
	},
	onPullDownRefresh() {
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	methods: {
		/* 单个编辑 */
		cancelFavorite(item, index) {
			let _this = this
			uni.showModal({
				title: '确认要取消收藏这款宝贝吗？',
				content: '高漫数位板触…',
				success: function(res) {
					if (res.confirm) {
						_this.itemList.splice(index, 1)
						uni.showToast({
							title: '操作成功',
							icon: 'none'
						});
					}
				}
			});
		},
		/* 页面跳转 */
		jump2item(item, index) {
			uni.navigateTo({
				url: '/pages/item/index'
			})
		},
	}
}
